import {
	delRepArr
} from "@/common/util/index.js"
export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			screenHeight: this.screenHeight,
			hidden: true,
			listCurID: '',
			list: [],
			listCur: '',
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		};
	},
	onLoad() {
		this.init();
		// let list = [{}];
		// for (let i = 0; i < 26; i++) {
		// 	list[i] = {};
		// 	list[i].name = String.fromCharCode(65 + i);
		// }
		// this.list = list;

	},
	onReady() {
		let that = this;
		uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
			that.boxTop = res.top
		}).exec();
		uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
			that.barTop = res.top
		}).exec()
	},
	onPullDownRefresh() {
		this.init();
	},
	methods: {
		init() {
			this.api.home.hotVip.get_all_list(null, res => {
				console.log(res)
				console.log(delRepArr(res.data))
				this.list = delRepArr(res.data)
				this.listCur = this.list[0];
			})
		},
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		//获取文字信息
		getCur(e) {
			this.hidden = false;
			this.listCur = this.list[e.target.id].letter;
		},
		setCur(e) {
			this.hidden = true;
			this.listCur = this.listCur
		},
		//滑动选择Item
		tMove(e) {
			let y = e.touches[0].clientY,
				offsettop = this.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				this.listCur = that.list[num].letter
			};
		},

		//触发全部开始选择
		tStart() {
			this.hidden = false
		},

		//触发结束选择
		tEnd() {
			this.hidden = true;
			if (this.listCur == '#') {
				this.scrollTop = this.old.scrollTop
				this.$nextTick(function() {
					this.scrollTop = 0
				});
			} else {
				this.listCurID = this.listCur
			}
		},
		indexSelect(e) {
			let that = this;
			let barHeight = this.barHeight;
			let list = this.list;
			let scrollY = Math.ceil(list.length * e.detail.y / barHeight);
			for (let i = 0; i < list.length; i++) {
				if (scrollY < i + 1) {
					that.listCur = list[i].name;
					that.movableY = i * 20
					return false
				}
			}
		},
		goDetail(id) {
			uni.navigateTo({
				url: "../detail/detail?id=" + id
			})
		},
		toggleFollowed(el, index, sub) {
			this.api.home.hotVip.toggle_followed({
				vid: el.userId,
				action: el.isFollowed  ? 0 : 1
			}, res => {
				console.log(res)
				this.list[index].list[sub].isFollowed  = !this.list[index].list[sub].isFollowed 
			})
		}
	}
}
